<template>
  <v-container
    class="fill-height align-start"
    fluid
  >
    <v-container
      fluid
    >
      <v-card>
        <v-card-title class="font-weight-bold">
          {{ $t('现金总额') }}:
          <span class="pl-4">
            {{ currentAmount }} Euro
          </span>
        </v-card-title>
        <v-card-text class="pt-3">
          <v-row>
            <v-col cols="12">
              <v-text-field
                v-model="amountCashBook"
                :label="$t('现金支出，直接使用数字')"
                :placeholder="$t('请输入金额')"
                :rules="[() => !!amountCashBook || 'This field should not be empty']"
              />
            </v-col>
            <v-col cols="12">
              <v-text-field
                v-model="noteCashBook"
                :label="$t('remark')"
                :placeholder="$t('请添加备注')"
              />
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-btn
                block
                large
                class="primary"
                @click="saveCashBook"
              >
                <v-icon left>
                  mdi-content-save-outline
                </v-icon>
                {{ $t('保存') }}
              </v-btn>
            </v-col>
          </v-row>
        </v-card-text>
      </v-card>

      <ik-data-table

        :model="model"
        :entity-name="$t('现金账')"
        :icon="'mdi-devices'"
        :filter="filter"
        :use-default-action="false"
        :use-select="false"
        :use-edit-action="false"
        class="mt-5"
      >
        <template v-slot:filterLeft>
          <v-menu
            ref="menu"
            v-model="menu"
            :close-on-content-click="false"
            :return-value.sync="dates"
            offset-y
          >
            <template v-slot:activator="{ on }">
              <v-text-field
                v-model="dates"
                class="ml-auto"
                hide-details
                :label="$t('日期筛选')"
                prepend-icon="mdi-calendar"
                readonly
                style="max-width: 250px;"
                single-line
                append-icon="mdi-close"
                @click:append="clear"
                v-on="on"
              />
            </template>
            <v-date-picker
              v-model="dates"
              no-title
              range
              scrollable
              locale="de"
            >
              <v-spacer/>
              <v-btn
                text
                color="primary"
                @click="menu = false"
              >
                {{ $t('Cancel') }}
              </v-btn>
              <v-btn
                text
                color="primary"
                @click="$refs.menu.save(dates)"
              >
                {{ $t('OK') }}
              </v-btn>
            </v-date-picker>
          </v-menu>
        </template>
      </ik-data-table>
    </v-container>
  </v-container>
</template>

<script>

  import CashBook from '../../model/CashBook'
  import { getCurrentCashAmount } from '@/components/Utlis/api'
  import dayjs from 'dayjs'
  import { IkDataTable } from 'metaflow-js'
  import { manageCashAccount } from '../../components/Utlis/api'
  import Utils from '../../components/Utlis/Utils'

  const today = dayjs().format('YYYY-MM-DD')
  // const todayDateRange = ['2021-12-06', '2021-12-06']
  const todayDateRange = [today, today]
  export default {
    components: { IkDataTable },
    data: function () {
      return ({

        // model: CashBook,
        model: CashBook,
        dates: todayDateRange,
        amountCashBook: null,
        noteCashBook: '',
        menu: false,
        currentAmount: -1,

      })
    },
    computed: {

      filter () {
        if (Date.parse(this.dates[0]) > Date.parse(this.dates[1])) {
          [this.dates[0], this.dates[1]] = [this.dates[1], this.dates[0]]
        }

        const _data = {
          fromDate: this.dates[0],
          toDate: this.dates[1],
        }

        return _data
      },

    },
    watch: {},

    async created () {
      // this.dates = [formatDate(0), formatDate(0)]
      // this.dates = [dayjs().format('YYYY-MM-DD'), formatDate(0)]

      await this.showCashAmount()

    // this.datevForm = this.getAllDatevSettings()
    // console.log('todayDateRange', todayDateRange)
    },
    methods: {

      async initialize () {
        await this.showCashAmount()
        this.clear()
      },
      clear () {
        this.dates = [today, today]
      },

      async showCashAmount () {
        this.currentAmount = await getCurrentCashAmount()
      },

      async saveCashBook () {
        if (this.amountCashBook) {
          /**/
          await manageCashAccount(-this.amountCashBook, this.noteCashBook)
          Utils.toast('保存成功')
          await this.initialize()
        } else {
          Utils.showError('金额不能为空')
        }
      },

    },
  }

</script>
